import React from 'react';
import { connect } from 'react-redux';
import * as actionTypes from './store/actionsTypes'
import './App.css'

function App({input1,input2,operator,equation,readInput,addOperator,performOperation,clear}) {
    
    const INPUT= 'input'
    const OPERATOR ='opertor'
    const buttonTypes ={INPUT,OPERATOR}
    const buttons=[
        {type:buttonTypes.INPUT,value:1},
        {type:buttonTypes.INPUT,value:2},
        {type:buttonTypes.INPUT,value:3},
        {type:buttonTypes.INPUT,value:4},
        {type:buttonTypes.INPUT,value:5},
        {type:buttonTypes.INPUT,value:6},
        {type:buttonTypes.INPUT,value:7},
        {type:buttonTypes.INPUT,value:8},
        {type:buttonTypes.INPUT,value:9},
        {type:buttonTypes.INPUT,value:0},
        {type:buttonTypes.OPERATOR,value:'+'},
        {type:buttonTypes.OPERATOR,value:'-'},
        {type:buttonTypes.OPERATOR,value:'*'},
        {type:buttonTypes.OPERATOR,value:'/'}
   ]
   const Button = (props)=>{
       return <button className="pointer grid-item text-white" onClick={()=>props.handler(props.value)}>
           {props.value}
       </button>
   }
  return <div className='app'>
        <div className='header'><h2 className='text-center'>CALCULATOR</h2></div>
        <div className='user-display'><h4 className='text-left'>{equation}</h4><h3 className='text-right'>{operator?input2:input1}</h3></div>
        <div className='grid-2 calc'>
            <div className='grid-3'>
                {buttons.filter(button=>button.type===buttonTypes.INPUT).map(button=>{
                    return <Button {...button} handler={readInput} key={button.value} />
                })}
                <Button value='.' handler={readInput}/>
                <Button value='=' handler={performOperation}/>
            </div>
            <div className='grid-1'>
                {buttons.filter(button=>button.type===buttonTypes.OPERATOR).map(button=>{
                    return <Button {...button} handler={addOperator} key={button.value} />
                })}
                <Button value='CE' handler={clear}/>
            </div>
        </div>
  </div>;
}

const mapStateToProps = ({input1=0,input2=0,operator='',equation=''})=>{
    return {
        input1,
        input2,
        operator,
        equation
    }
}

const mapDispatchToProps = (dispatch)=>{
    return {
        readInput:(val)=>dispatch({
            type:actionTypes.CALC.READ_USER_INPUT,
            payload:val
        }),
        addOperator:(val)=>dispatch({
            type:actionTypes.CALC.ADD_OPERATOR,
            payload:val
        }),
        performOperation:()=>dispatch({
            type:actionTypes.CALC.PERFORM_OPERATION
        }),
        clear:()=>dispatch({
            type:actionTypes.CALC.CLEAR_INPUT
        })
    }
}

export default connect(mapStateToProps,mapDispatchToProps)(App)
