import actionsTypes from "../actionsTypes";
export default function calcReducer(state={},action){
    switch(action.type){
        case actionsTypes.CALC.READ_USER_INPUT:
            let newState = {...state}
            if(newState.operator)
                newState['input2']=newState['input2']?newState['input2']+action.payload:String(action.payload)
            else
                newState['input1']=newState['input1']?newState['input1']+action.payload:String(action.payload)
            return {...newState}
        case actionsTypes.CALC.CLEAR_INPUT:
            return {}
        case actionsTypes.CALC.ADD_OPERATOR:
            return {...state,equation:'',operator:action.payload}
        case actionsTypes.CALC.PERFORM_OPERATION:
            let result
            if(!(state.operator) && state.prevOperator){
                state.operator = state.prevOperator
                state.input2 = state.prevInput2
            }
            let equation = `${parseFloat(state.input1)}${state.operator}${parseFloat(state.input2)}`
            // eslint-disable-next-line no-eval
            if(state.operator) result =eval(equation)
            if(result)
                return {
                    input1:String(parseFloat(result.toFixed(9))),
                    prevInput2:state.input2,
                    prevOperator:state.operator,
                    equation:`(${equation})=`
                }
            return state
        default:
            return state
    }
}